import './index.css';
import { Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { useHistory } from 'react-router';
import { formatNumber } from '../../helpers/formatNumber';
export const OrderReceived = () => {
    const history = useHistory();
    const order = history.location.state;
    return (
        <div className="order">
            <Container>
                <div className="order-notice">
                    <p>Cm n bn. n hng  c nhn</p>
                    <p>M n hng ca bn l <span>{order?._id}</span></p>
                </div>
                {order ? <div className="order-overview">
                    <ul>
                        <li>
                            <p>M n hng</p>
                            <p>{order._id}</p>
                        </li>
                        <li>
                            <p>Ngy</p>
                            <p>{new Date(order.createdAt).toLocaleDateString()}</p>
                        </li>
                        <li>
                            <p>Email</p>
                            <p>{order.email}</p>
                        </li>
                        <li>
                            <p>Tng cng</p>
                            <p>{formatNumber(order.total)}</p>
                        </li>
                        <li>
                            <p>Phng thc thanh ton</p>
                            <p>Tr tin mt khi giao hng</p>
                        </li>
                    </ul>
                </div> : ""}
                <div className="back-home">
                    <Link to="/Bookstore">Tip tc mua sm</Link>
                </div>
            </Container>
        </div>
    )
}