import { Row, Col } from "react-bootstrap";

import { useEffect, useRef } from "react";
import { useForm } from 'react-hook-form';
export const Profile = (props) => {
    const { register, handleSubmit, reset } = useForm();
    const password = useRef({});
    const fileRef = useRef();
    const { user, onSubmitEditProfile, image, onChangeFileImage, statusProfile, setStatusProfile } = props;
    useEffect(() => {
        reset("form");
    }, [statusProfile])
    return (
        <div className="profile_wrap">
            <Row className="profile-mb">
                <Col lg={8}>
                    <div className="profile_form">
                        <h3>H s ca bn</h3>
                        <form onSubmit={handleSubmit(onSubmitEditProfile)}>
                            <div className="profile_form-group">
                                <label>Email: </label>
                                <p>{user.email}</p>
                            </div>
                            <div className="profile_form-group">
                                <label>H: </label>
                                {statusProfile === true ? <input
                                    {...register("form.firstName", { required: true })}
                                    type="text"
                                /> : <span>{user.firstName}</span>}
                            </div>
                            <div className="profile_form-group">
                                <label>Tn: </label>
                                {statusProfile === true ? <input
                                    type="text"
                                    {...register("form.lastName", { required: true })}
                                /> : <span>{user.lastName}</span>}
                            </div>
                            <div className="profile_form-group">
                                <label>S in thoi: </label>
                                {statusProfile === true ? <input
                                    type="text"
                                    {...register("form.phone", { required: true })}
                                /> : <span>{user.phone}</span>}
                            </div>
                            {statusProfile === true ? <div className="profile_form-group">
                                <label>Mt khu: </label>
                                <input
                                    type="password"
                                    ref={password}
                                    {...register("form.password")}
                                    placeholder="B trng nu khng i..."
                                />
                            </div> : ""}
                            {statusProfile === true ? <div className="profile_form-group">
                                <label>Nhp li mt khu: </label>
                                <input
                                    {...register("form.confirmPassword")}
                                    type="password"
                                    placeholder="B trng nu khng i..."
                                />
                            </div> : ""}
                            <div className="profile_form-btn">
                                {statusProfile === true ? <button>Lu</button> : ""}
                            </div>
                        </form>
                        {statusProfile === false ? <button className="btn-profile_edit" onClick={() => setStatusProfile(true)}>Sa</button>
                            : <button className="btn-profile_edit" onClick={() => setStatusProfile(false)}>Quay li</button>}
                    </div>
                </Col>
                <Col lg={4}>
                    <div className="profile-avatar">
                        <img
                            src={image}
                            alt="nh i din"
                        />
                        {statusProfile === true ? <p onClick={() => fileRef.current.click()}>Chn nh</p> : ""}
                        <input
                            type="file"
                            accept="image/png, image/jpeg"
                            hidden
                            ref={fileRef}
                            onChange={onChangeFileImage}
                        />
                    </div>
                </Col>
            </Row>
        </div>
    )
}