import { Spinner } from '../../../components/Spinner';
import { useForm } from 'react-hook-form';
export const SignUp = (props) => {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const { changeStatus, onSubmitRegister, isLoading } = props;
    return (
        <div className="form-container">
            <h2 className="auth-title">ng k</h2>
            <div className="auth-des">
                <p className="auth-meta">Bn l thnh vin ?</p>
                <span onClick={() => changeStatus(true)} >ng nhp</span>
            </div>
            <form className="auth-form" onSubmit={handleSubmit(onSubmitRegister)}>
                <div className="auth-form_group">
                    <label>Email</label>
                    <div>
                        <input
                            {...register("email", { required: true, pattern: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/ })}
                            type="text"
                        />
                    </div>
                    {errors.email && <span className="error-auth">Trng ny phi l email</span>}
                </div>
                <div className="auth-form_group">
                    <label>H</label>
                    <div>
                        <input
                            type="text"
                            {...register("firstName", { required: true })}
                        />
                    </div>
                    {errors.firstName && <span className="error-auth">Trng ny l bt buc</span>}
                </div>
                <div className="auth-form_group">
                    <label>Tn</label>
                    <div>
                        <input
                            type="text"
                            {...register("lastName", { required: true })}
                        />
                    </div>
                    {errors.lastName && <span className="error-auth">Trng ny l bt buc</span>}
                </div>
                <div className="auth-form_group">
                    <label>S in thoi</label>
                    <div>
                        <input
                            type="text"
                            {...register("phone", { required: true })}
                        />
                    </div>
                    {errors.phone && <span className="error-auth">Trng ny l bt buc</span>}
                </div>
                <div className="auth-form_group">
                    <label>Mt khu</label>
                    <div>
                        <input
                            type="password"
                            {...register("password", {
                                required: "Trng ny l bt buc", minLength: {
                                    value: 6,
                                    message: "Mt khu cha t nht 6 k t"
                                }
                            })}
                        />
                    </div>
                    {errors.password && <span className="error-auth">{errors.password.message}</span>}
                </div>
                <button className="auth-form_btn">{isLoading ? <Spinner bgC="#5386e4" /> : "ng k"}</button>
            </form>
        </div>
    )
}