import { Col, Container, Row } from "react-bootstrap"
import { Button } from "../../components/Button"
import { AiFillPhone, AiTwotoneMail, AiFillFacebook } from 'react-icons/ai';
import { HiLocationMarker } from 'react-icons/hi';
import { CgWebsite } from 'react-icons/cg';
import { BiHelpCircle } from 'react-icons/bi';
import emailjs from 'emailjs-com';
import { Spinner } from '../../components/Spinner';
import { toast } from 'react-toastify';
import { toastConfig } from '../../constants/configToast';
import { useState } from "react";
import "./index.css";
export const Contact = () => {
    const [isLoading, setIsLoading] = useState(false);
    const onSubmitForm = (e) => {
        setIsLoading(true);
        e.preventDefault();
        emailjs.sendForm("service_ek2seqq", "template_3i8drd7", e.target, "user_Ye2TEIAtUzzAVwSHhnDUg")
            .then((result) => {
                toast(" gi thnh cng!", toastConfig);
                e.target.reset();
                setIsLoading(false);
            }, (error) => {
                e.target.reset();
                toast(error.text, toastConfig);
                setIsLoading(false);
            });
    }
    return (
        <div className="contact-wrap">
            <Container>
                <div className="form-wrap">
                    <Row>
                        <Col xs={12}>
                            <form onSubmit={onSubmitForm}>
                                <div className="form-group_contact">
                                    <input type="text" placeholder="Tn ca bn.." name="name" />
                                </div>
                                <div className="form-group_contact">
                                    <input type="text" placeholder="Email ca bn.." name="email" />
                                </div>
                                <div className="form-group_contact">
                                    <textarea type="text" placeholder="Ni dung..." rows={30} col={45} name="content" />
                                </div>
                                <div>
                                    <Button
                                        width="30%"
                                        label={isLoading ? <Spinner /> : "Gi"}
                                    />
                                </div>
                            </form>
                        </Col>
                        <Col xs={12}>
                            <div className="contact-main">
                                <div>
                                    <span><AiFillPhone /></span>
                                    <span>0896728429</span>
                                </div>
                                <div>
                                    <span><AiTwotoneMail /></span>
                                    <span>hp.sliver123@gmail.com</span>
                                </div>
                                <div>
                                    <span><AiFillFacebook /></span>
                                    <a href="https://www.facebook.com/profile.php?id=100010467708316">Hong Phc</a>
                                </div>
                                <div>
                                    <span><BiHelpCircle /></span>
                                    <span>Tr gip</span>
                                </div>
                                <div>
                                    <span><HiLocationMarker /></span>
                                    <span >Khm 1, Phng 4, TP Sa c, ng Thp</span>
                                </div>
                                <div>
                                    <span><CgWebsite /></span>
                                    <a href="https://phuctran2901.github.io/Portfolio-cv/">My resume</a>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
            </Container>
        </div >
    )
}